html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: "Alegreya", serif;
}

* {
  margin: 0;
  padding: 0;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

section {
  border: 0.2rem black solid;
  padding: 1rem;
}

.counter-heading {
  margin: 2rem 0;
  font-size: 6rem;
}

.tablo {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.tablo-text {
  font-size: 5rem;
}

.actions {
  display: flex;
  justify-content: center;
  list-style: none;
}

.actions-item {
  margin-right: 2rem;
}

.actions-item:last-child {
  margin-right: 0;
}

.btn {
  width: 5rem;
  height: 3rem;
}
